import route from './route'

import BetBox from '../../components/bet-box'

export default {
  name: route.key,

  components: {
    BetBox
  },

  data() {
    return {
      route,
      drawList: null,
      hasError: false,
      isLoading: true
    }
  },

  computed: {
    data() {
      return this.drawList ? this.drawList : []
    }
  },

  async mounted() {
    try {
      this.drawList = await this.$store.dispatch('bet/list')
    } catch(e) {
      this.$message({
        type: 'error',
        message: e.message
      })
    }

    this.isLoading = false
  },

  methods: {

  }
}



