import moment from 'moment'

import Auth from '../../mixins/auth'

import PlayerSearch from '../player-search'

import Utils from '../../utils'

export default {
  name: 'BetBox',

  mixins: [
    Auth
  ],

  components: {
    PlayerSearch
  },

  props: [
    'draw'
  ],

  data() {
    return {
      player: {
        agent_id: null,
        player_id: null,
        contact_no: null,
        draw_id: null,
        max_accumulated: null,
        combinations: []
      },
      playerRules: {
        // player_id: [
        //   {
        //     required: true,
        //     message: 'Please select a player',
        //     trigger: 'blur'
        //   }
        // ],
        contact_no: [
          {
            required: true,
            message: 'Please input contact #',
            trigger: 'blur'
          }
        ]
      },
      entry: {
        bet: 0,
        isramble: 'S',
        input: ''
      },
      entryRules: {
        input: [
          {
            required: true,
            message: 'Please input combination',
            trigger: 'blur'
          }
        ]
      },
      showDialog: false,
      inProgress: false
    }
  },

  computed: {
    maxInput() {
      let {
        numberformat,
        maxinput
      } = this.draw

      if (numberformat == '0-9') {
        return maxinput
      }

      return 6
    },

    dialog() {
      return this.draw ? {
        title: this.draw.name,
        date: moment().format('MMMM DD, YYYY')
      } : null
    }
  },

  mounted() {
    this.reset()
  },

  methods: {
    handleClose(done) {
      if (typeof done == 'function') {
        done()
      }
    },

    reset() {
      this.player = {
        agent_id: this.$user.id,
        player_id: null,
        contact_no: null,
        draw_id: this.draw.id,
        max_accumulated: this.draw.max_accumulated,
        combinations: []
      }

      this.entry = {
        bet: this.draw.minimumbet,
        isramble: this.draw.draw_type == '3d_rambol' ? 'R' : 'S',
        input: ''
      }
    },

    openDialog() {
      this.showDialog = true
    },

    closeDialog() {
      this.showDialog = false
      this.reset()
    },

    isNumber(evt) {
      evt = (evt) ? evt : window.event;
      var charCode = (evt.which) ? evt.which : evt.keyCode;
      if ((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46) {
        evt.preventDefault()
      } else {
        return true
      }
    },

    removeEntry(entry) {
      let index = this.player.combinations.indexOf(entry)

      if (index > -1) {
        this.player.combinations.splice(index, 1)
      }
    },

    clearEntries() {
      this.player.combinations = []
    },

    async handleEntry() {
      let draw = this.draw
      let entry = this.entry

      if (draw.numberformat == '0-9') {
        let combination = this.entry.input.split('')

        if (draw.draw_type == '3d_rambol') {
          if (entry.isramble == 'R') {
            let combArr = Utils.Math.permutation(combination)
            let bet = entry.bet / combArr.length
            let entries = combArr.map(d => {
              return {
                combination: d.join('-'),
                isramble: entry.isramble,
                bet
              }
            })

            this.player.combinations = this.player.combinations.concat(entries)
          } else {
            this.player.combinations.push({
              combination: combination.join('-'),
              bet: entry.bet,
              isramble: entry.isramble
            })
          }
        } else {
          this.player.combinations.push({
            combination: combination.join('-'),
            bet: entry.bet,
            isramble: entry.isramble
          })
        }
      }
    },

    async queue() {
      await this.handleEntry()

      this.entry = {
        bet: this.draw.minimumbet,
        isramble: this.draw.draw_type == '3d_rambol' ? 'R' : 'S',
        input: ''
      }
    },

    async send() {
      if (this.inProgress) return

      try {
        this.inProgress = true

        let results = await this.$store.dispatch('bet/send', this.player)

        console.log(results)

        this.$message({
          type: 'success',
          message: 'Bet successful!'
        })
      } catch(e) {
        this.$message({
          type: 'error',
          message: 'Failed to send'
        })
      }

      this.inProgress = false
    }
  }
}



