

export default {
  name: 'PlayerSearch',

  props: [
    'value'
  ],

  data() {
    return {
      username: ''
    }
  },

  methods: {
    async querySearchAsync(search, cb) {
      try {
        let data = await this.$store.dispatch('player/search', search)

        cb(data.map(d => {
          return {
            id: d.id,
            value: d.username
          }
        }))
      } catch(e) {
        this.$message({
          type: 'error',
          message: e.message
        })
      }
    },

    clear() {
      this.$emit('input', null)
    },

    handleSelect(item) {
      this.$emit('input', item.id)
    }
  }
}


